<template>
	<div class="myfile-container" v-loading="loading">
		<div class="btn">
			<el-button size="medium" v-if="item_count==0">上传</el-button>
			<el-button size="medium" v-if="item_count==0">新建</el-button>
			<el-button size="medium" v-if="item_count!=0" @click="downLoad()">下载</el-button>
			<el-button size="medium" v-if="item_count!=0">删除</el-button>
			<el-button size="medium" v-if="item_count==1">编辑</el-button>
			<el-button size="medium" v-if="item_count==1">预览</el-button>
			<el-button size="medium" v-if="item_count!=0">更多</el-button>
		</div>

		<div class="file-info">
			<ns-checkbox @click.native="selectAll" ref="selectAll"></ns-checkbox>
			<div class="filename">文件名</div>
			<div class="info">
				<div class="size">大小</div>
				<div class="time">修改时间</div>
				<div class="view">
					<i class="el-icon-s-fold" @click="toggleView(1)"></i>
					<i class="el-icon-menu" @click="toggleView(0)"></i>
				</div>
			</div>
		</div>

		<no-file v-show="fileItem==''?true:false"></no-file>

		<div class="file-list">
			<ul :class="class_change?'list':'grid'">
				<li
					v-for="(item ,index) in fileItem"
					:key="index"
					class="li"
					:class="{bg:item.showBg==1}"
					@click="selectLi(index,item)"
					@click.right.prevent="showMenu($event)"
					@dblclick="preView(item.type)"
				>
					<div class="grid-box">
						<div class="inner-box">
							<ns-checkbox ref="check" class="checkbox"></ns-checkbox>
							<img :src="require('@/../public/type/'+item.type+'.png')" alt />
						</div>
						<div class="name-box">
							<div
								class="filename"
								:class="{'folder':item.type=='folder'}"
								@click.stop="preView(item.type)"
							>{{item.name}}</div>
							<div class="fun-ico">
								<i class="el-icon-download" @click.stop="downLoad()"></i>
								<i class="el-icon-share" @click.stop="share()"></i>
								<i class="el-icon-more" @click.stop="more()"></i>
							</div>
						</div>
					</div>
					<div class="info">
						<div class="size">{{item.size}}</div>
						<div class="time">{{item.time}}</div>
						<div></div>
					</div>
				</li>
			</ul>
			<el-divider>共 {{item_total}} 项</el-divider>
		</div>
	</div>
</template>

<script>
import NsCheckbox from '@/components/NsCheckbox/CheckBox'
import NoFile from '@/components/NoFileView/index'

export default {
	components: {
		NsCheckbox,
		NoFile
	},
	mounted() {
		this.item_total = this.fileItem.length

		for (let i = 0; i < this.fileItem.length; i++) {
			this.$set(this.fileItem[i], 'showBg', 0)
			//console.log(this.fileItem[i].showBg)
		}
	},
	data() {
		return {
			fileItem: [
				{
					name: 'aaa',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'folder'
				},
				{
					name: 'bbb',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'ccc',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'ddd',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'psd'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'txt'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				},
				{
					name: 'eee',
					size: '112kB',
					time: '2021-04-09 09:33',
					type: 'cad'
				}
			],
			select_item: [],
			item_total: 0,
			item_count: 0,
			class_change: 1, // list/grid view
			loading: false
		}
	},
	methods: {
		selectAll: function() {
			var check_len = this.$refs.check.length

			if (this.item_total == this.item_count) {
				this.select_item = []
				this.item_count = 0

				for (let i = 0; i < check_len; i++) {
					this.$refs.check[i].selected = false
					this.fileItem[i].showBg = 0
					//console.log(this.fileItem[i].showBg)
				}
			} else {
				this.select_item = []
				for (let i = 0; i < this.fileItem.length; i++)
					this.select_item.push(this.fileItem[i])

				this.item_count = this.item_total

				for (let i = 0; i < check_len; i++) {
					this.$refs.check[i].selected = true
					this.fileItem[i].showBg = 1
					//console.log(this.fileItem[i].showBg)
				}
			}

			console.log(this.select_item)
		},
		select: function(index, el) {
			if (this.select_item.includes(el)) {
				this.select_item.splice(this.select_item.indexOf(el), 1)
				this.item_count--
			} else {
				this.select_item.push(el)
				this.item_count++
			}

			this.$refs.check[index].selected = !this.$refs.check[index].selected

			console.log(this.select_item)
		},
		toggleView: function(a) {
			this.class_change = a
		},
		selectLi: function(index, item) {
			item.showBg = !item.showBg
			this.select(index, item)
			//console.log(index, item.showBg)
		},
		preView: function(type) {
			alert(type)
		},
		downLoad: function() {
			alert('click downLoad')
		},
		share: function() {
			alert('click share')
		},
		more: function() {
			alert('click more')
		},
		showMenu: function() {
			alert('right')
		}
	},
	watch: {
		item_count: function() {
			if (this.item_count === this.item_total)
				this.$refs.selectAll.selected = !this.$refs.selectAll.selected
			if (this.item_count != this.item_total)
				this.$refs.selectAll.selected = false
		}
	}
}
</script>


<style lang="scss" scoped>
ul,
li {
	padding: 0;
	margin: 0;
	list-style: none;
}

.bg {
	background-color: #dfeeff;
}

.fun-ico {
	display: none;
	position: absolute;
	right: 0;

	i {
		margin: 0 5px;
		color: #107ffc;
		font-size: 20px;
		cursor: pointer;
	}
}

.folder {
	transition: 0.2s;
	&:hover {
		text-decoration: underline;
		cursor: pointer;
	}
}

.el-divider--horizontal {
	width: 200px;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);

	.el-divider__text.is-center {
		background-color: rgb(243 251 252);
	}
}

.info {
	display: flex;
	position: absolute;
	width: 40%;
	right: 50px;
	& > div {
		width: 50%;
	}

	.view {
		text-align: right;
		i {
			transition: 0.2s;
			cursor: pointer;
			margin: 0 5px;

			&:hover {
				color: rgb(115, 210, 248);
			}
		}
	}
}

.myfile-container {
	height: inherit;
	font-size: 15px;

	.btn {
		padding: 10px 20px;

		.el-button--medium {
			padding: 10px 35px;
		}
	}

	.file-info {
		background-color: #f9f9f9;
		padding: 5px 10px;
		display: flex;
	}

	.file-list {
		padding: 5px 0;
		width: 100%;
		height: calc(100% - 90px);
		overflow: scroll;
		position: relative;

		&::-webkit-scrollbar {
			display: none;
		}

		.list {
			.li {
				position: relative;
				height: 50px;
				display: flex;
				align-items: center;
				border-bottom: 1px solid rgb(241, 241, 241);
				transition: 0.1s;
				padding: 0 10px;

				&:hover {
					background-color: #cecece42;
				}
				&:hover .fun-ico {
					display: block;
				}

				.grid-box {
					display: flex;
					align-items: center;
					position: relative;
					width: 50%;

					.inner-box {
						display: flex;

						img {
							width: 35px;
							height: 35px;
							margin-right: 15px;
							margin-left: 2px;
						}
					}

					.name-box {
						display: flex;
						width: 100%;
						justify-content: space-between;
					}
				}
			}
		}
	}
}
.grid {
	display: flex;
	flex-wrap: wrap;

	.li {
		width: 150px;
		//height: 150px;
		transition: 0.2s;

		.grid-box {
			margin: 10% 10% 15% 10%;
			text-align: center;

			.inner-box {
				border: 1px solid rgb(216, 216, 216);
				box-sizing: border-box;
				padding: 20px;
				position: relative;
				transition: 0.2s;
				cursor: pointer;

				&:hover {
					background-color: rgba(95, 184, 236, 0.267);
				}

				.checkbox {
					position: absolute;
					margin: 0;
					top: 5px;
					left: 5px;
				}

				img {
					height: 100%;
					width: 100%;
				}
			}

			.filename {
				margin: 5px 0;
			}
		}

		.info,
		.fun-ico {
			display: none;
		}
	}
}
</style>